// Глобальные стили

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: $font-family-circle;
  display: grid;
}

body {
  grid-template-rows: min-content auto min-content;
}

a {
  text-decoration: none;
}

